import React, { useState } from 'react'
import styles from '../scss/Cart.module.scss'
import Uptitle from '../component/Uptitle'
import { useDispatch, useSelector } from 'react-redux'
import {deleteItem} from "../store.js"
import { useNavigate } from 'react-router-dom'


const Cart = () => {
  const state = useSelector((state)=>{
    return state
  })
  const cart = state.cart;

  let navigate = useNavigate();
  let dispatch = useDispatch()
  let cart_delete = () =>{
      dispatch(deleteItem);
  }
  //
  let total = 0;
  for(var i=0;i<cart.length;i++){
    total= total + cart[i].price;
  }
  //  
  let unit = (val) =>{ 
    return String(val).replace(/(\d)(?=(?:\d{3})+(?!\d))/g, '$1,');
  }
  return (
    <>
        <div className={styles.wrap}>
          <div className={styles.title}>
              <Uptitle h1 ="" p=""/>
          </div>
          <div className={styles.content_box}>
              <form name="cart" id="cart" method='post'>
                <div className={styles.table_box}>
                  <table>
                    <thead>
                      <tr>
                        <th><input type="checkbox"/></th>
                        <th></th>
                        <th></th>
                        <th></th>
                        <th></th>
                      </tr>
                    </thead>
                    <tbody>
                    {
                      cart.map((v,i) =>
                      <tr key={i}> 
                        <td><input type="checkbox"/></td>
                        <td className={styles.table_tbody_pro}>
                          <div className={styles.table_img} style={{backgroundImage:`url(${process.env.PUBLIC_URL+v.imgUrl})`}}>
                          </div>
                          <div className={styles.table_artdetail}>
                            <div className={styles.table_title}>
                              <h4>{v.title}</h4><p>{v.artist}<span>&nbsp;&nbsp;(+{unit(v.oriprice)})</span></p>
                            </div>
                            <p> : {v.size}<span>&nbsp;(+{unit(v.sizeadd)})</span><br/>
                               : {v.media}<span>&nbsp;(+{unit(v.mediaadd)})</span><br/>
                               : {v.flame}<br/>
                              () : {v.matt}</p>
                          </div>
                        </td>
                        <td><p>{unit(v.oriprice+v.sizeadd+v.mediaadd)}</p></td>
                        <td><p>{v.number}</p></td>
                        <td><p>{unit(v.price)}</p></td>
                      </tr>
                      )
                     }
                    </tbody>
                  </table>
                  <div className={styles.table_button}>
                    <button></button>
                    <button onClick = {() => {cart_delete(); navigate("/cart");}} > </button>
                  </div>
                </div>
              </form>
              <div className={styles.totalpirce}>
                <ul>
                  <li>
                    <span> </span>
                    <strong>{unit(total)}</strong>
                    <span></span>
                  </li>
                  <li>
                    <i>+</i>
                    <span></span>
                    <strong>0</strong>
                    <span></span>
                  </li>
                  <li>
                    <i>=</i>
                    <span> </span>
                    <strong>{unit(total)}</strong>
                    <span></span>
                  </li>
                </ul>
              </div>
              <div className={styles.final_button}>
                <button> </button>
                <button> </button>
              </div>
          </div>
        </div>
    </>
  )
}

export default Cart