import React, { useEffect, useState } from 'react'
import styles from '../scss/Detail.module.scss'
import {Title2 , DetailMenu } from '../styles/emotion';
import { useParams ,useNavigate } from 'react-router-dom';
import { useSelector } from 'react-redux';
import {addItem} from "../store.js"
import { useDispatch } from 'react-redux';

const Detail = () => {

    let navigate = useNavigate(); //  
    //data  
    const state = useSelector((state)=>{
      return state
    })
    const art = state.art;

    //section   
    let [isActive, setIsActive] = useState(0);
    let [direction, setDirection] = useState(0);

    let imgchange = (e)=>{
    let nodes = [...e.target.parentElement.children];
    let index = nodes.indexOf(e.target);
    let content1 =  e.target.parentElement.parentElement.children[0];
    setIsActive(index);
    if(index===0){
    content1.style.backgroundImage="url('/img/item-detail-frame-bg01.jpg')";
    }else if(index ===1){content1.style.backgroundImage="url('/img/item-detail-frame-bg02.jpg')";}
     else if(index ===2){content1.style.backgroundImage="url('/img/item-detail-frame-bg03.jpg')";}
     else if(index ===3){content1.style.backgroundImage="url('/img/item-detail-frame-bg04.jpg')";}
     else if(index ===4){content1.style.backgroundImage="url('/img/item-detail-frame-bg05.jpg')";} 
    }

    //get id   
    const {id} = useParams();
  
      // 

      let dispatch = useDispatch()
      let [size,setSize] = useState(0);
      let size_arr = ["30.00cm x 30.00cm","35.00cm x 35.00cm"];
      let [sizeAdd , setSizeAdd] = useState(0);
      let [media,setMedia]  = useState(0);
      let media_arr = ["",""];
      let [mediaAdd , setMediaAdd] = useState(0);
      let [flame,setFlame]  = useState(0);
      let flame_arr = ["","","","","","D","","","",""]
      let flame_addprice = 0;
      let [matt,setMatt] = useState(0);
      let matt_arr = [""];
      let matt_addprice = 0;
      let [price,setPrice] = useState(art[id].price);
      let [value,setValue] = useState(1);

      let setsize = (e) => {
        setSize(e.target.value);
        if(e.target.value === "1"){
        setPrice(price + (10000*value));
        setSizeAdd(sizeAdd+(10000*value));
        }else{
          setPrice(price - (10000*value));
          setSizeAdd(sizeAdd-(10000*value));
        }
      }
      let [mod,setMod] =useState(0);
      let setmedia = (e) => {
        const nodes = [...e.target.parentElement.parentElement.children];
        const index = nodes.indexOf(e.target.parentElement);
        setMedia(index);
        if(index === 1 && mod === 0){
          setPrice(price + (10000*value));
          setMediaAdd(mediaAdd+(10000*value));
          setMod(index);
        }else if(index === 0){
          if(mod === 1){
          setPrice(price - (10000*value));
          setMediaAdd(mediaAdd-(10000*value));
          setMod(index);
          }
        }
      }
      let setflame = (e) => {
        const nodes = [...e.target.parentElement.parentElement.children];
        const index = nodes.indexOf(e.target.parentElement);
        setFlame(index);
        if(index === 2 || index === 3 ){
          flame_addprice = flame_addprice + 10000;
        }else{
          flame_addprice = 0;
        }
      }
      let setmatt = (e) => {
        const nodes = [...e.target.parentElement.parentElement.children];
        const index = nodes.indexOf(e.target.parentElement);
        setMatt(index);
        if(index === 1 ){
          matt_addprice = matt_addprice + 10000;
        }else{
          matt_addprice = 0;
        }
      }
      

    // 
    let priceplus = () =>{
      if(value>0){
      setValue(value + 1);
      let v = value + 1;
      setPrice(((art[id].price*v)+(sizeAdd*v)+(mediaAdd*v)));
      //setPrice(String((art[id].price+sizeAdd+mediaAdd)*(value+1)).replace(/(\d)(?=(?:\d{3})+(?!\d))/g, '$1,'));
      }
    }
    let priceminus = () =>{
      if(value>1){
        setValue(value - 1);
        let v = value - 1;
        setPrice(((art[id].price*v)+(sizeAdd*v)+(mediaAdd*v)));
      }
    }
    // 
    let in_cart = () =>{
      dispatch(addItem({id:art[id].id,
                        title:art[id].title,
                        artist:art[id].artist,
                        item:art[id].title,
                        imgUrl:art[id].imgUrl,
                        size:size_arr[size],
                        media:media_arr[media],
                        flame:flame_arr[flame],
                        matt:matt_arr[matt],
                        oriprice:art[id].price,
                        price:price,
                        number:value,
                        sizeadd:sizeAdd,
                        mediaadd:mediaAdd  
                      }))
    }
    //  
    const artist = state.artist;
    let artist_num =0;
      for(var i=0; i<artist.length;i++){
          if(artist[i].name ===  art[id].artist){
            artist_num = i;
          }
      }
      let artbox = [];
      for(var i=0;i<art.length;i++){
        if(art[id].artist === art[i].artist && art[id].id != art[i].id ){
            artbox.push(art[i].id);
        }
      }

      useEffect(() => {
        if(art[id].width === 'vertical'){ // class   
          setDirection(1);
        }else{
          setDirection(0);
        }
    }, [art[id].width,price]);
      
  return (
    <>
      <div className={styles.wrap}>
        <div className={styles.contents}>
          <div className={styles.content1}>
            <div className={styles.content1_backimg}></div>
            <div className={styles.content1_purchasebox}>
              <div className={styles.content1_frameimg}>
                <div className={direction === 0 ?  styles.content1_img : styles.content1_img_sero} style={{backgroundImage:`url(${process.env.PUBLIC_URL+art[id].imgUrl})`}}></div>
              </div>
              <div className={styles.content1_purchase}>
                <div className={styles.content1_purchase_tit}>
                  <h2>{art[id].title}</h2>
                  <p>{art[id].artist}</p>
                </div>
                <div className={styles.content1_purchase_opt}>
                  <div className={styles.purchase_opt}>
                    <div className={styles.purchase_opt_title}> </div>
                    <select className={styles.opt} name="size" placeholder=' .' onChange={setsize}>
                      <option value="select" disabled> .</option>
                      <option value="0" >30.0cm x 30.0cm</option>
                      <option value="1" >35.0cm x 35.0cm</option>
                    </select>
                  </div>
                  <div className={styles.purchase_opt}>
                    <div className={styles.purchase_opt_title}> </div>
                    <div className={styles.purchase_opt_flame}>
                      <div className={media === 0 ? styles.opt_flame_img_on : styles.opt_flame_img}>
                        <div className={styles.flame_img} onClick={setmedia}>
                          <img src={process.env.PUBLIC_URL+'/img/item-detail-step01-fineart.jpg'}/>
                        </div>
                        
                      </div>
                      <div className={media === 1 ? styles.opt_flame_img_on : styles.opt_flame_img}>
                        <div className={styles.flame_img} onClick={setmedia}>
                          <img src={process.env.PUBLIC_URL+'/img/item-detail-step01-fineart.jpg'}/>
                        </div>
                        
                      </div>
                    </div>
                  </div>
                  <div className={styles.purchase_opt}>
                    <div className={styles.purchase_opt_title}> </div>
                    <div className={styles.purchase_opt_flame}>
                      <div className={flame === 0 ? styles.opt_flame_img_on : styles.opt_flame_img}>
                        <div className={styles.flame_img} onClick={(e) => {setflame(e);}}>
                          <img src={process.env.PUBLIC_URL+'/img/item-detail-step02-canvas-pannel.jpg'}/>
                        </div>
                        
                      </div>
                      <div className={flame === 1 ? styles.opt_flame_img_on : styles.opt_flame_img}>
                        <div className={styles.flame_img} onClick={setflame}>
                          <img src={process.env.PUBLIC_URL+'/img/item-detail-step02-wrapping-canvas.jpg'} />
                        </div>
                        
                      </div>
                      <div className={flame === 2 ? styles.opt_flame_img_on : styles.opt_flame_img}>
                        <div className={styles.flame_img} onClick={setflame}>
                          <img src={process.env.PUBLIC_URL+'/img/item-detail-step02-float.jpg'} />
                        </div>
                        
                      </div>
                      <div className={flame === 3 ? styles.opt_flame_img_on : styles.opt_flame_img}>
                        <div className={styles.flame_img} onClick={setflame}>
                          <img src={process.env.PUBLIC_URL+'/img/item-detail-step02-wood-float.jpg'} />
                        </div>
                        
                      </div>
                      <div className={flame === 4 ? styles.opt_flame_img_on : styles.opt_flame_img}>
                        <div className={styles.flame_img} onClick={setflame}>
                          <img src={process.env.PUBLIC_URL+'/img/item-detail-step02-up-wood.jpg'} />
                        </div>
                        
                      </div>
                      <div className={flame === 5 ? styles.opt_flame_img_on : styles.opt_flame_img}>
                        <div className={styles.flame_img} onClick={setflame}>
                          <img src={process.env.PUBLIC_URL+'/img/item-detail-step02-antique-d-sliver.jpg'} />
                        </div>
                        D
                      </div>
                      <div className={flame === 6 ? styles.opt_flame_img_on : styles.opt_flame_img}>
                        <div className={styles.flame_img} onClick={setflame}>
                          <img src={process.env.PUBLIC_URL+'/img/item-detail-step02-wood-beige.jpg'} />
                        </div>
                        
                      </div>
                      <div className={flame === 7 ? styles.opt_flame_img_on : styles.opt_flame_img}>
                        <div className={styles.flame_img} onClick={setflame}>
                          <img src={process.env.PUBLIC_URL+'/img/item-detail-step02-box-white.jpg'} />
                        </div>
                        
                      </div>
                      <div className={flame === 8 ? styles.opt_flame_img_on : styles.opt_flame_img}>
                        <div className={styles.flame_img} onClick={setflame}>
                          <img src={process.env.PUBLIC_URL+'/img/item-detail-step02-box-wood.jpg'} />
                        </div>
                        
                      </div>
                      <div className={flame === 9 ? styles.opt_flame_img_on : styles.opt_flame_img}>
                        <div className={styles.flame_img} onClick={setflame}>
                          <img src={process.env.PUBLIC_URL+'/img/item-detail-step02-box-black.jpg'} />
                        </div>
                        
                      </div>
                    </div>
                  </div>
                  <div className={styles.purchase_opt}>
                    <div className={styles.purchase_opt_title}>()</div>
                    <div className={styles.purchase_opt_flame}>
                      <div className={matt === 0 ? styles.opt_flame_img_on : styles.opt_flame_img}>
                        <div className={styles.flame_img} onClick={setmatt}>
                          <img src={process.env.PUBLIC_URL+'/img/item-detail-step04-none.jpg'} />
                        </div>
                          
                      </div>
                    </div>
                  </div>                
                </div>
                <div className={styles.content1_purchase_selopt}>
                  <div className={styles.seloptBox}>
                  {art[id].title}
                    <div className={styles.selopt_Count}>
                      <button onClick={priceminus}>-</button>
                      <input type="text" value={value}/>
                      <button onClick={priceplus}>+</button>
                    </div>
                  </div>
                </div>
                <div className={styles.content1_purchase4_price}>
                  <span></span>
                  <span>{String(price).replace(/(\d)(?=(?:\d{3})+(?!\d))/g, '$1,')}<span></span></span>
                </div>
                <div className={styles.content1_purchase5_selbtn}>
                  <button onClick={() => {in_cart(); navigate("/cart");
                  
                  }}></button>
                  <button></button>
                </div>
                <div className={styles.content1_purchase6_sns}></div>
              </div>
            </div>
            <div className={styles.content1_list_wrap}>
              <div className={isActive === 0 ? styles.content1_list_on : styles.content1_list } style ={{backgroundImage:`url(${process.env.PUBLIC_URL+"/img/item-detail-frame-bg01.jpg"})`}} onClick={imgchange}></div>
              <div className={isActive === 1 ? styles.content1_list_on : styles.content1_list } style ={{backgroundImage:`url(${process.env.PUBLIC_URL+"/img/item-detail-frame-bg02.jpg"})`}} onClick={imgchange}></div>
              <div className={isActive === 2 ? styles.content1_list_on : styles.content1_list } style ={{backgroundImage:`url(${process.env.PUBLIC_URL+"/img/item-detail-frame-bg03.jpg"})`}} onClick={imgchange}></div>
              <div className={isActive === 3 ? styles.content1_list_on : styles.content1_list } style ={{backgroundImage:`url(${process.env.PUBLIC_URL+"/img/item-detail-frame-bg04.jpg"})`}} onClick={imgchange}></div>
              <div className={isActive === 4 ? styles.content1_list_on : styles.content1_list } style ={{backgroundImage:`url(${process.env.PUBLIC_URL+"/img/item-detail-frame-bg05.jpg"})`}} onClick={imgchange}></div>
            </div>
          </div>
          <div className={styles.content2}>
            <h1> </h1>
            <div className={styles.content2_box}>
                <ul>
                  {
                    artbox.map((v,i) =>{
                      return(
                        <li>
                          <img src={process.env.PUBLIC_URL+art[v].imgUrl} onClick={()=>navigate(`/detail/${art[v].id}`)}/>
                          <div className={styles.content2_title}><h4>{art[v].title}</h4></div>
                          <div className={styles.content2_artist}><h5>{art[v].artist}</h5></div>
                          <div className={styles.content2_size}><h6>30.0 x 30.0</h6></div>
                          <div className={styles.content2_price} style ={{display:'flex' ,lineHeight:'30px'}}><h4>{art[v].price}</h4><p></p></div>
                        </li>
                      )
                    })
                  }
                </ul>
            </div>
          </div>
          <div className={styles.content3}>
            <div className={styles.content3_box}>
              <DetailMenu>
                <ul>
                  <li><a className="on" href="#"></a></li>
                  <li><a href="#"></a></li>
                  <li><a href="#"></a></li>
                  <li><a href="#"></a></li>
                </ul>
              </DetailMenu>
              <div className={styles.content3_explain_box}>
                <div className={styles.explain_item}>
                  <div className={styles.explain_item_tit}>
                    <Title2>
                      <h1 className={styles.title_span}>{art[id].title}</h1>
                    </Title2>
                  </div>
                  <div className={styles.explain_item_frame}>
                    <img src={process.env.PUBLIC_URL+art[id].imgUrl} />
                  </div>
                  <div className={styles.explain_item_desc}>
                    <div className={styles.item_desc_tit}>
                      <h2>{art[id].title}</h2><h5>{artist[artist_num].english_name}</h5><h5>{art[id].artist}</h5>
                    </div>
                    <div className={styles.item_desc}>
                      <p>{art[id].content1}</p>
                      <p>{art[id].content2}</p>
                    </div>
                  </div>
                </div>
                <div className={styles.explain_artist}>
                  <div className={styles.artist_img_box}>
                    <div className={styles.artist_img} style={{backgroundImage:`url(${process.env.PUBLIC_URL+artist[artist_num].imgUrl})`}}></div>
                  </div>
                  <div className={styles.artist_desc}>
                    <div className={styles.artist_name}>
                      <h2>{art[id].artist}</h2>
                    </div>
                    <table>
                      <tr>
                        <td></td>
                        <td>{artist[artist_num].nationality}</td>
                      </tr>
                      <tr>
                        <td></td>
                        <td>{artist[artist_num].birth_death}</td>
                      </tr>
                      <tr>
                        <td></td>
                        <td>{artist[artist_num].works}</td>
                      </tr>
                    </table>
                    <div className={styles.artist_detail}>
                      <span>{artist[artist_num].content1}</span><br />
                      <span>{artist[artist_num].content2}</span>
                      
                    </div>
                  </div>
                </div>
                <div className={styles.explain_print}></div>
              </div>
              <DetailMenu>
                <ul>
                  <li><a href="#"></a></li>
                  <li><a className="on" href="#"></a></li>
                  <li><a href="#"></a></li>
                  <li><a href="#"></a></li>
                </ul>
              </DetailMenu>
              <div className={styles.content3_purchase_info_box}>
                <Title2>
                  <h1 className={styles.title_span}>  </h1>
                </Title2>
                <div className={styles.purchase_infos}>
                  <div className={styles.purchase_info}>
                    <h3><span>1</span> </h3>
                    <p>         1~3cm          .              , 1:1     .</p>
                  </div>
                  <div className={styles.purchase_info}>
                    <h3><span>2</span></h3>
                    <p>           ,             .         ,              ,     A/S  /    .</p>
                  </div>
                  <div className={styles.purchase_info}>
                    <h3><span>3</span></h3>
                    <p>                   5 (    20 )  .      .</p>
                    <p></p>
                    <p>*   ,      .</p>
                  </div>
                  <div className={styles.purchase_info}>
                    <h3><span>4</span></h3>
                    <p>   4,000 10    .       ,     .</p>
                  </div>
                  <div className={styles.purchase_info}>
                    <h3><span>5</span>  A/S</h3>
                    <p>  AS  1 .          , AS    A/S . A/S        , 1:1, (010-0000-0000)  (grin@xxxxxxx.co.kr) .</p>
                  </div>
                </div>
              </div>
              <div className={styles.content3_exchange_refund_box}>
                <Title2>
                  <h1 className={styles.title_span}>/</h1>
                </Title2>
                <div className={styles.purchase_infos}>
                  <div className={styles.purchase_info}>
                    <h3><span>1</span>  </h3>
                    <p>  , , ,                .    .             .      ,          .</p>
                  </div>
                  <div className={styles.purchase_info}>
                    <h3><span>2</span> / </h3>
                    <p>,      7 ( )   .          AS ,         . ,                      .</p>
                  </div>
                  <div className={styles.purchase_info}>
                    <h3><span>3</span>/</h3>
                    <p>             .     ,    30%,     70%       .</p>
                  </div>
                  <div className={styles.purchase_info}>
                    <h3><span>4</span></h3>
                    <p>   , A/S       / .    / ,   , A/S     .</p>
                    <p></p>
                    <p>*  :    1234 ()</p>
                  </div>
                  <div className={styles.purchase_info}>
                    <h3><span>5</span>  A/S</h3>
                    <p>  AS  1 .          , AS    A/S . A/S        , 1:1, (010-0000-0000)  (grin@xxxxxxx.co.kr) .</p>
                  </div>
                </div>
              </div>
            </div>  
          </div>
        </div>
      </div>
    </>
  )
}

export default Detail